/**
 * 
 */
const saveStorage = (key, data) => {
    return new Promise((resolve, reject) => {
        uni.setStorage({
            key: key,
            data: data,
            success() {
                resolve();
            },
            fail() {
                reject();
            }
        })
    })
}
/**
 * 
 */
const removeStorageByKey = (key) => {
    return new Promise((resolve, reject) => {
        uni.removeStorage({
            key: key,
            success() {
                resolve();
            },
            fail() {
                reject();
            }
        })
    })
}

/**
 * 
 */
const getStorageByKey = (key) => {
    return new Promise((resolve, inject) => {
        uni.getStorage({
            key: key,
            success(res) {
                resolve(res.data);
            }, fail() {
                inject('' + key + '')
            }
        })
    })
}


